<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    #t_1_4
    .titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.4.	Protocolos de seguridad en recorridos turísticos
     
    .row.justify-content-center.align-items-center.mb-2(data-aos="fade-left")
      p La seguridad es uno de los pilares fundamentales en la operación de servicios turísticos. Un recorrido turístico, por más sencillo que parezca, implica el desplazamiento de personas por entornos naturales, urbanos o culturales que pueden presentar diferentes riesgos. Por ello, los protocolos de seguridad permiten anticipar, prevenir y actuar ante posibles situaciones de emergencia, garantizando la integridad física, emocional y patrimonial de los usuarios, así como la reputación del servicio turístico.

      .col-md-3
        figure
          img(src='@/assets/curso/temas/tema1/tema1/tema1-img5.svg', alt='El diseño y la organización de paquetes turísticos')

      .col-md-8.mb-4.mb-md-0.my-2
        p El guía o conductor de grupo tiene la responsabilidad de planificar la seguridad antes, durante y después del recorrido, aplicando medidas preventivas, verificando equipos y coordinando con las autoridades locales. Además, debe brindar una inducción clara a los turistas sobre las normas básicas de comportamiento, autocuidado y respeto por el entorno. Según la Organización Mundial del Turismo (OMT, 2018), “la gestión de la seguridad turística debe integrarse en la planificación general del destino, promoviendo la coordinación entre operadores, guías, autoridades y comunidad local”.
        p A continuación se explican los principios básicos de seguridad turística:
    
     
    AcordionA(tipo="a" clase-tarjeta="tarjeta tarjeta--azul acordeonA")(data-aos="flip-left")
      .row(titulo="Prevención").letra-acordeon-titulos
        .col-md-1
        .col-md-7
          p
          p.color-adicional-4 Consiste en anticiparse a los riesgos mediante una planificación adecuada y el reconocimiento del terreno, identificando posibles peligros naturales, técnicos o humanos que puedan afectar el recorrido.
        .col-md-3
          figure
              img(src='@/assets/curso/temas/tema1/tema4/rec8-img1.svg', alt='Texto que describa la imagen')

      .row(titulo="Preparación").letra-acordeon-titulos
        .col-md-1
        .col-md-7
          p
          p.color-adicional-4 Implica contar con equipos, recursos y conocimientos necesarios para responder ante emergencias, incluyendo botiquines, comunicación de emergencia, rutas de evacuación y capacitación en primeros auxilios.
        .col-md-3
          figure
              img(src='@/assets/curso/temas/tema1/tema4/rec8-img2.svg', alt='Texto que describa la imagen')

      .row(titulo="Comunicación").letra-acordeon-titulos
        .col-md-1
        .col-md-7
          p.color-adicional-4 Se basa en informar claramente a los turistas sobre las normas de seguridad, el itinerario, las condiciones del entorno y las medidas de prevención. Una comunicación oportuna y transparente fortalece la confianza y el autocuidado.
        .col-md-3
          figure
              img(src='@/assets/curso/temas/tema1/tema4/rec8-img3.svg', alt='Texto que describa la imagen')
      
      .row(titulo="Coordinación").letra-acordeon-titulos
        .col-md-1
        .col-md-7
          p.color-adicional-4 Requiere trabajar en conjunto con entidades locales, organismos de socorro, autoridades ambientales y servicios de salud, garantizando una respuesta articulada y eficiente ante cualquier eventualidad.
        .col-md-3
          figure
              img(src='@/assets/curso/temas/tema1/tema4/rec8-img4.svg', alt='Texto que describa la imagen')
      
      .row(titulo="Responsabilidad compartida").letra-acordeon-titulos
        .col-md-1
        .col-md-7
          p.color-adicional-4 Reconoce que tanto el operador turístico como los turistas deben cumplir las normas establecidas, promoviendo una cultura de seguridad colectiva y el respeto por las disposiciones locales.
        .col-md-3
          figure
              img(src='@/assets/curso/temas/tema1/tema4/rec8-img5.svg', alt='Texto que describa la imagen')
      

    br
    .row.justify-content-center.align-items-center.mb-2(data-aos="fade-left")
      p Sumado a lo anterior, es fundamental reconocer las diferentes etapas de aplicación de los protocolos de seguridad, que se pueden identificar antes, durante y después del recorrido; en dónde cada etapa tiene los siguientes aspectos:

    .row.justify-content-center(data-aos="fade-right")
      .col-md-10.mb-5.mb-md-0
        LineaTiempoD
          .row(numero="Antes del recorrido&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;" titulo="")
            .col-md-3
              br
              figure
                img(src='@/assets/curso/temas/tema1/tema4/rec9-img1.svg', alt='Texto que describa la imagen')
                
            .col-md-9.mb-4.mb-md-0
              ol.lista-ol--cuadro
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Verificar el estado del transporte, los equipos y la documentación necesaria (botiquín, seguros, permisos, lista de pasajeros).
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Consultar los pronósticos meteorológicos y ajustar las actividades si las condiciones no son favorables.
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Identificar los puntos críticos del recorrido (pendientes, corrientes de agua, zonas resbaladizas o de difícil acceso).
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Realizar una charla de inducción con los participantes, informando sobre las normas, los tiempos, las rutas previstas y el comportamiento ambiental responsable.

          .row(numero="Durante el recorrido&nbsp;&nbsp;&nbsp;&nbsp;" titulo="")
            .col-md-3
              br
              figure
                img(src='@/assets/curso/temas/tema1/tema4/rec9-img2.svg', alt='Texto que describa la imagen')
                
            .col-md-9.mb-4.mb-md-0
              ol.lista-ol--cuadro
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Supervisar constantemente el ritmo del grupo y la integridad de los usuarios.
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Mantener comunicación permanente entre guías, conductores y coordinadores logísticos.
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Aplicar pausas activas e hidratación periódica, según las condiciones climáticas y físicas del grupo.
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Controlar los accesos y salidas, evitando que algún participante se separe del grupo o se desoriente en el trayecto.

          .row(numero="Después del recorrido" titulo="")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema1/tema4/rec9-img3.svg', alt='Texto que describa la imagen')
            
            .col-md-9.mb-4.mb-md-0
              ol.lista-ol--cuadro
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Verificar el regreso seguro de todos los participantes y el estado de los equipos.
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Elaborar un reporte de novedades o incidentes, registrando observaciones relevantes.
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Evaluar el cumplimiento de las medidas de seguridad, identificando fortalezas y aspectos por mejorar para garantizar la mejora continua del servicio.

    br
    br
    .row.justify-content-center(data-aos="fade-left")
      .col-lg-9
        figure
          img(src='@/assets/curso/temas/tema1/tema4/tema14-tabla2.svg', alt='Texto que describa la imagen')      


    br
    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p Promover la seguridad no solo previene accidentes, sino que también refuerza la confianza del visitante, fomenta el respeto por la naturaleza y garantiza la sostenibilidad del destino. Una cultura de seguridad turística implica educación, prevención y corresponsabilidad, integrando a guías, operadores, comunidades locales y visitantes en la protección del entorno y el bienestar colectivo. Un turista informado y un guía preparado son los mejores aliados para un turismo responsable y sostenible. “La seguridad en el turismo no se improvisa; se construye con conocimiento, planificación y empatía hacia quienes confían en nuestros servicios.”

    .row.content-center.align-items-center.mb-4(data-aos="fade-left")
      p La figura 4 ejemplifica un protocolo de seguridad para una caminata ecológica

